.MainView {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}

#root {
  height: 100%;
}

html {
  height: 100%;
}

body {
  height: 100%;
  background-image: url('./Images/background.jpg');
  background-size: cover;
  background-position-y: -5vw;
}

@font-face {
  font-family: wow;
  src: url(./Fonts/LifeCraft_Font.ttf);
}

.searchButton {
  width: 10vh;
  font-family: wow;
  font-size: 2vh;
  padding: 1vh;
  color: white;
  background: url('./Images/searchBackground.webp');
  margin-bottom: 9px;
  margin-left: 1vh;
}

.backButton {
  font-family: wow;
  color: white;
  background: url('./Images/searchBackground.webp');
}

#searchInput {
  width: 60%;
  padding: 1.2vh;
}

.mainMenu {
  max-width: 40rem !important;
  margin: 2vh;
  min-width: 40rem !important;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url('./Images/inputBackground.jpg');
}

.mainMenuCardBody {
  text-align: center;
  width: 100%;
}

.result-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}